"use client"
import postService from "@/services/postService";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";

const MyCart = () => {

    const [mycart, setmycart] = useState([]);
    const [error, setError] = useState(null);
    const [user, setUser] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const router = useRouter(); 
        
            const fetchReview = async () => {
            try {
                const response = await postService.getMyCarts();
                setmycart(response.data.mycart);
                setError(null); 
            } catch (error) {
                if (error.response) {
                setError(`Server Error: ${error.response.status} - ${error.response.data.message || "Something went wrong"}`);
                } else if (error.request) {
                setError("Network error: Unable to reach the server. Please try again.");
                } else {
                setError("An unexpected error occurred.");
                }
            }
            };
        
            useEffect(() => {
            fetchReview();
            }, []);

            useEffect(() => {
                fetch("http://localhost:8001/api/checkAuth", {
                  method: "GET",
                  credentials: "include",
                })
                  .then((res) => res.json())
                  .then((data) => {
                    if (data.isAuthenticated) {
                      setUser({
                        id: data.userId,
                        username: data.username,
                        email: data.email,
                        profile: data.profile? data.profile:null
                      });
                      setIsLoading(false);
                    }
                  });
              }, [router]);


  return (
    <div>
      <p>{user?.username}</p>
      {user?.profile && (
                  <img src={`http://localhost:8001/gmailprofile/${user?.profile}`} alt="User Logo" className="rounded-full w-10 h-10 cursor-pointer" onClick={() => setIsOpen(!isOpen)} />
                )}
      <h1>My Cart</h1>
      {mycart?.map((items, id)=>{
        return (<div key={id}>
            <img src={`http://localhost:8001/items/${items.service_id.photo}`} alt="photo" className="h-24 w-24 object-cover"/>
            <h2 key={id}>{items.service_id.name}</h2>
            <p>{items.service_id.amount}</p>
        </div>)
    })}
            {error && <p style={{ color: "red" }}>{error}</p>}
            </div>
  )
}

export default MyCart